// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import store from './store/store'
import router from './router'
import './assets/styles/reset.css'
import './assets/styles/border.css'
import App from './App'

Vue.config.productionTip = false

// 注册doubletap事件
var VueTouch = require('vue-touch')
VueTouch.registerCustomEvent('doubletap', {
  type: 'tap',
  taps: 2
})

Vue.use(VueTouch, {name: 'v-touch'})

// 页面禁止用户缩放
window.onload = function () {
  document.addEventListener('touchstart', function (event) {
    if (event.touches.length > 1) {
      event.preventDefault()
    }
  })
  document.addEventListener('gesturestart', function (event) {
    event.preventDefault()
  })
}

// 每次切换组件或刷新均会执行(挂载vue实例之前就要执行！！)
if (window.Storage && window.localStorage && window.localStorage instanceof Storage) {
  // 在页面加载时读取localStorage
  if (localStorage.getItem('store')) {
    store.replaceState(Object.assign({}, store.state, JSON.parse(localStorage.getItem('store'))))
  }
  setInterval(() => { // 每10s把状态保存到localStorage一次
    localStorage.setItem('store', JSON.stringify(store.state))
    console.log('saved')
  }, 10000)
  // 在页面刷新时将store保存到localStorage里(safari貌似不支持beforeunload)
  window.addEventListener('beforeunload', () => {
    localStorage.setItem('store', JSON.stringify(store.state))
  })
} else {
  alert('您的浏览器 不支持localStorage，将会影响使用！')
}

/* eslint-disable no-new */
new Vue({ // 使用vue-router时，要最后挂载App根实例，不然会导致配置不成功!
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



