import Vue from 'vue'
import Router from 'vue-router'
import store from '@/store/store'
import Login from '@/components/Login'
import Home from '@/components/Home'
import Add from '@/components/Add'
import Update from '@/components/Update'
import HomeList from '@/components/HomeList'
import HomeUser from '@/components/HomeUser'
import About from '@/components/About'
import BusTest from '@/components/BusTest'
import NoFound from '@/components/NoFound'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [{
    path: '/',
    component: Login,
    meta: {requiresAuth: true} // 添加表示需要验证
  }, {
    path: '/home', // 路由不区分大小写
    // redirect: '/home/list',
    component: Home,
    meta: {requiresAuth: true},
    children: [{
      path: 'list', // 需验证，父路由已添加requiresAuth，这里不需添加
      component: HomeList
    }, {
      path: 'user',
      name: 'homeuser',
      component: HomeUser
    }]
  }, {
    path: '/about',
    name: 'ab', // router-view渲染时，指定的渲染位置
    component: About
  }, {
    path: '/bus',
    component: BusTest
  }, {
    path: '/add',
    component: Add,
    meta: {requiresAuth: true} // 添加表示需要验证
  }, {
    path: '/update',
    component: Update,
    meta: {requiresAuth: true} // 添加表示需要验证
  }, {
    path: '/404',
    name: 'notFound',
    component: NoFound
  }, {
    path: '*', // 此处需特别注意置于最底部
    redirect: '/404'
  }]
})
router.beforeEach((to, from, next) => { // 路由保卫监控 登录状态
  console.log(to.matched)
  if (to.matched.some(record => record.meta.requiresAuth)) { // 需要验证登录状态的页面路由
    // 这里判断用户是否登录，验证本地存储是否有token
    if (!store.state.status && to.path !== '/') { // 未登录,原路由却指向非登录页
      next('/') // 重定向到登录页
      console.log('11' + store.state.status)
    } else if (store.state.status && (to.path === '/')) { // 已登录,原路由却指向登录页
      next('/home/list') // 重定向到主页
      console.log('22' + store.state.status + to.path)
    } else {
      console.log('33' + store.state.status + to.path)
      next() // 【必须有！！！否则当不满足以上2条件，页面将无法进行转向】
    }
  } else {
    console.log('44')
    next() // 确保一定要调用 next()
  }
})
export default router



