<template>
  <div id="home-user">
    <div class="bg"></div>
    <p>HomeUser组件</p>
    <!-- <img src="@/assets/nezha.png"> -->
    <p>切换组件后，组建之中的数据会及时更新</p>
    <p>{{msg}}</p>
    <h2 class="hello">{{welcome}}</h2>
