<script type="text/javascript">
import store from '@/store/store'
import Menu from '@/components/menu.js'
export default {
  name: 'home组件',
  data () {
    return {
      msg: store.state.count,
      mark: 2,
      welcome: '又是元气满满的一天！'
    }
  },
  created () {
    Menu.$emit('mark', 2) // 将当前页面mark标记为2，通过事件总线Menu，准备与兄弟组件Home进行通信
  },
  methods: {
    deleteAll () {
      var ask = confirm('警告：此操作将会 清空所有信息，再次使用需要重新注册！')
      if (ask) {
        store.commit('clearAll')
      }
    },
    quit () {
      store.commit('changeStatus')
      this.$router.push('/')
    }
  }
}
</script>

